<template>
  <div id="userManage">
    <QueryBox @searchSrldId="searchSrldId">
      <Button type="success" size="small" icon="md-add" class="btn-add" style="margin-right: 10px" @click.stop="openAdd">新建</Button>
    </QueryBox>
    <div class="tab_box">
      <Table ref="table" border stripe :loading="loading" :columns="columns1" :data="data1"  size="small" highlight-row :max-height="tableHeight"></Table>
    </div>
    <div class="page_box" id="pageBox">
      <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" size="small" @on-change="pageChange" show-total/>
    </div>
    <Modal v-model="modal1"
           :mask-closable="true"
           title="新建服务">
      <Form :model="formItem" ref="add" :rules="AddRule" :label-width="100">
        <FormItem label="版本名称" prop="version_id">
          <Select filterable clearable v-model="formItem.version_id">
            <Option v-for="(it, ind) in versionList" :value="it.value" :key="ind">{{ it.label }}</Option>
          </Select>
        </FormItem>
        <FormItem label="服务文件">
          <input type="file" ref="file" accept=".zip">
        </FormItem>
        <FormItem label="服务名称" prop="name">
          <Input v-model="formItem.name" placeholder="请输入服务名称"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading2" @click="addSubmit">确定</Button>
      </div>
    </Modal>
    <Modal v-model="modal2"
           :mask-closable="true"
           title="编辑服务">
      <Form :model="formItem1" ref="edit" :rules="EditRule" :label-width="100">
        <FormItem label="版本名称" prop="version_id">
          <Select filterable clearable v-model="formItem1.version_id">
            <Option v-for="(it, ind) in versionList" :value="it.value" :key="ind">{{ it.label }}</Option>
          </Select>
        </FormItem>
        <FormItem label="服务文件">
          <input type="file" ref="file1" accept=".zip">
        </FormItem>
        <FormItem label="服务名称" prop="name">
          <Input v-model="formItem1.name" placeholder="请输入服务名称"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading1" @click="submitUpdate">确定</Button>
      </div>
    </Modal>
    <!-- 查看问题点-->
    <div class="mapBox" :class="{addActive:addActive}">
      <mapBox ref="mapBox" :seeId="mapSeeId" :znzStatus="znzStatus" :blcStatus="blcStatus" @signOut="signOut"></mapBox>
    </div>
  </div>
</template>

<script>
import { infoDelete, infoInsert, infoUpdate, infoSelect, versionList } from '@/api/dataCenter'
import QueryBox from './template_query'
import mapBox from './mapBox'
import { renderTime } from '@/utils/common'
export default {
  name: 'dataCenterLc',
  data () {
    return {
      /* 查询条件 */
      name: '',
      /* 表格动态高度 */
      tableHeight: window.innerHeight - 290,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 删除数据id */
      delId: '',
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        // {
        //   title: '版本名称',
        //   key: 'version_id'
        // },
        {
          title: '服务名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '审核状态',
          key: 'audit',
          render: (h, params) => {
            let auditStatus = params.row.audit
            let btnType = ''
            if (auditStatus === 0) {
              auditStatus = '无需审核'
              btnType = 'default'
            } else if (auditStatus === 1) {
              auditStatus = '待审核'
              btnType = 'primary'
            } else if (auditStatus === 2) {
              auditStatus = '审核通过'
              btnType = 'success'
            } else if (auditStatus === 3) {
              auditStatus = '审核未通过'
              btnType = 'error'
            }
            return h('div', [
              // 审核
              h('Tag', {
                props: {
                  color: btnType
                }
              }, auditStatus)
            ])
          },
          align: 'center'
        },
        {
          title: '变更时间',
          key: 'updatetime',
          render: (h, params) => {
            return h('span', renderTime(params.row.updatetime))
          },
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 270,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 查看地图
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small',
                    icon: 'ios-map'
                  },
                  style: {
                    marginRight: '5px',
                    background: 'rgb(16,186,204)',
                    borderColor: 'rgb(16,186,204)'
                  },
                  on: {
                    click: () => {
                      this.mapSeeId = params.row.id
                      this.znzStatus = params.row.compass
                      this.blcStatus = params.row.scale
                      this.addActive = true
                      this.$refs.mapBox.addShpLayer(params.row.ftp_shp)
                      this.$refs.mapBox.addTipLayer(params.row.compass, params.row.scale)
                    }
                  }
                }, '地图查看'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.modal2 = true
                    this.formItem1.name = params.row.name
                    this.formItem1.id = params.row.id
                    this.formItem1.version_id = params.row.version_id
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.delId = params.row.id
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该服务？',
                      onOk: () => {
                        this.sysDel()
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 加载 */
      loading: false,
      /* 加载 */
      loading1: false,
      /* 加载 */
      loading2: false,
      // 控制模态框的展示
      modal1: false,
      // 添加表格数据
      formItem: {
        name: '',
        version_id: ''
      },
      AddRule: {
        name: [
          { required: true, message: '服务名称不能为空', trigger: 'blur' }
        ],
        version_id: [
          { required: true, message: '请选择版本名称', trigger: 'change', type: 'number' }
        ]
      },
      // 修改表格数据
      formItem1: {
        id: '',
        name: '',
        version_id: ''
      },
      EditRule: {
        name: [
          { required: true, message: '服务名称不能为空', trigger: 'blur' }
        ],
        version_id: [
          { required: true, message: '请选择版本名称', trigger: 'change', type: 'number' }
        ]
      },
      // 控制模态框的展示
      modal2: false,
      // 版本列表
      versionList: [],
      /* 查看问题点弹框状态 */
      addActive: false,
      /* 查看地图的服务id */
      mapSeeId: '',
      znzStatus: '',
      blcStatus: ''
    }
  },
  components: {
    QueryBox,
    mapBox
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
    }.bind(this)
    this.getTableData()
    this.getVersion()
  },
  methods: {
    // 获取流程列表
    getVersion () {
      versionList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.forEach((item, index) => {
            this.versionList.push({
              label: item.name,
              value: item.id
            })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 获取列表数据 */
    getTableData () {
      this.loading = true
      infoSelect({
        data: {
          pageIndex: this.pageNum,
          pageSize: this.pageSize,
          name: this.name
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    openAdd () {
      this.modal1 = true
      this.$refs.add.resetFields()
      this.formItem.name = ''
      this.formItem.version_id = ''
      this.$refs.file.value = ''
    },
    // 提交上传
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          this.loading2 = true
          let fordata = new FormData()
          fordata.append('name', this.formItem.name)
          fordata.append('version_id', Number(this.formItem.version_id))
          fordata.append('file', this.$refs.file.files[0])
          infoInsert(fordata).then(res => {
            this.loading2 = false
            this.modal1 = false
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '新建服务成功' })
              this.$refs.file.value = ''
              this.getTableData()
            } else {
              this.$Notice.error({ title: '新建服务失败', desc: res.data.msg })
            }
          })
        }
      })
    },
    /* axios: 删除数据 */
    sysDel () {
      infoDelete({
        data: {
          id: this.delId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 打开修改
    openUpdate () {
      this.modal2 = true
      this.$refs.edit.resetFields()
      this.formItem1.id = ''
      this.formItem1.name = ''
      this.formItem1.version_id = ''
    },
    // 修改提交
    submitUpdate () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          this.loading1 = true
          let formdata = new FormData()
          formdata.append('name', this.formItem1.name)
          formdata.append('version_id', this.formItem1.version_id)
          formdata.append('id', this.formItem1.id)
          if (this.$refs.file1.value) {
            formdata.append('file', this.$refs.file1.files[0])
          }
          infoUpdate(formdata).then(res => {
            this.loading1 = false
            this.modal2 = false
            if (res.data.code === '0000') {
              this.$refs.file1.value = ''
              this.getTableData()
              this.$Notice.success({ title: '编辑成功' })
            } else {
              this.$Notice.error({ title: '编辑失败', desc: res.data.msg })
            }
          })
        }
      })
    },
    /* 查询框 */
    searchSrldId (obj) {
      this.name = obj.name
      this.pageNum = 1
      this.getTableData()
    },
    /* 分页跳转 */
    pageChange (p) {
      this.pageNum = p
      this.getTableData()
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
      if (addActive === false) {
        this.mapSeeId = ''
      }
    }
  }
}
</script>

<style scoped lang="scss">
  #userManage{
    .from_box{
      padding:10px;
      text-align: left;
    }
    .tab_box{
      padding: 0 15px;
    }
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      margin-left: 10px;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .mapBox {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
